import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import { data } from "./GeneVsProcedure.data";
import { AxisTick, AxisTickProps } from "@nivo/axes";
import { ResponsiveHeatmapCanvasClickable } from "./ResponsiveHeatMapCanvasClickable";
import "react-bootstrap-typeahead/css/Typeahead.css";
import { Typeahead } from "react-bootstrap-typeahead";
import { Option } from "react-bootstrap-typeahead/types/types";

function App() {
  const [selectedGenes, setSelectedGenes] = useState<Option[]>([]);
  const [chartData, setChartData] = useState<any>(data);
  return (
    <>
      <Typeahead
        id="basic-typeahead-multiple"
        multiple
        onChange={(selected) => {
          setSelectedGenes(selected);
          setChartData(
            selected.length ? data.filter((g) => selected.includes(g.id)) : data
          );
        }}
        options={data.map((g) => g.id)}
        placeholder="Filter by gene symbol..."
        selected={selectedGenes}
      />
      <div
        style={{
          height: `${
            selectedGenes.length ? selectedGenes.length * 20 + 500 : 10000
          }px`,
          marginRight: "10%",
          marginLeft: "10%",
          backgroundColor: "white",
          marginTop: "100px",
        }}
      >
        <ResponsiveHeatmapCanvasClickable
          data={chartData}
          margin={{ top: 100, right: 80, bottom: 20, left: 120 }}
          valueFormat={(v: any) => {
            const options = [
              "No data",
              "Images Available",
              "Images Available",
              "EA not significant -> LA significant",
              "Images and Automated Volumetric Analysis Available",
            ];
            return options[v];
          }}
          animate={false}
          axisTop={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: -45,
            legend: "",
            legendOffset: 50,
          }}
          axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: "",
            legendPosition: "middle",
            legendOffset: 60,
            renderTick: (tick: any) => (
              <ClickableAxisTick
                tick={tick}
                onClick={() =>
                  window.open(
                    `https://mousephenotype.org/data/genes/${
                      data[tick.tickIndex].mgiAccessionId
                    }`,
                    "_blank",
                    "noreferrer"
                  )
                }
              />
            ),
          }}
          axisRight={null}
          colors={(cell: any) => {
            const options = [
              "#ECECEC",
              "#17a2b8",
              "#ed7b25",
              "#ed7b25",
              "#17a2b8",
            ];
            return options[cell.value || 0];
          }}
          labelTextColor="black"
          emptyColor="#ccc"
          borderWidth={0.25}
          borderColor="#000"
          enableLabels={false}
          legends={[
            {
              anchor: "right",
              translateX: 50,
              translateY: 0,
              length: 200,
              thickness: 10,
              direction: "column",
              tickPosition: "after",
              tickSize: 3,
              tickSpacing: 4,
              tickOverlap: false,
              tickFormat: ">-.0s",
              title: "Value ",
              titleAlign: "middle",
              titleOffset: 4,
            },
          ]}
          annotations={[]}
          onClick={(cell: any) => console.log(cell.data)}
          //hoverTarget="row"
        />
      </div>
    </>
  );
}

const ClickableAxisTick = ({
  tick,
  onClick,
}: {
  tick: any;
  onClick: (tick: any) => void;
}) => {
  return <AxisTick {...tick} onClick={onClick} />;
};

export default App;
